/* eslint-disable react-hooks/exhaustive-deps */

import { useContext, useEffect, useReducer, useRef, useState } from "react";
import { QueryContext } from "../../../../App";
import { useGetOutlineQuery } from "../../../../api/outline-api";
import { BookMutation, Outline } from "../../../../types/types";
import ImageCropComponent from "./BookCoverImgCropper";
import { create } from "zustand";
import axios from "axios";
import flowAlert from "../../../../etc/flowAlert";

interface BookMutationStore {
  payload: BookMutation;
  isMutated: boolean;
  resetMutation: () => void;
  isLoading: boolean;
  dispatchs: {
    setTitle : (title : string) => void,
    setCoverImg : (coverImg : File) => void
  }
  resetPayload: () => void;
}

export const useBookMutationStore = create<BookMutationStore>((set : any) => ({
  payload: { title: null, coverImg: null }, //       
  isMutated: false, //   
  resetMutation: () => set(
    (state : any) => ({
      ...state,
      payload: { title: null, coverImg: null },
      isMutated: false
    })
  ),
  isLoading: false, //     .  false, [true  ,  false  ]   .
  dispatchs: { // bookMutation   dispatch .  isMutated true   .

    setTitle : (title : string) => {
      set((state : any) => ({
        payload: {
          ...state.payload,
          title: title
        },
        isMutated: true
      }));
    },

    setCoverImg : (coverImg : File) => {
      set((state : any) => ({
        payload: {
          ...state.payload,
          coverImg: coverImg
        },
        isMutated: true
      }));
    }

  },
  resetPayload: () => set(
    (state : any) => ({
      ...state,
      payload: { title: null, coverImg: null }
    })
  )
}));



export default function BookForm(){

  const { bookId } = useContext(QueryContext);
  const outline : Outline = useGetOutlineQuery(bookId);
  const bookStore = useBookMutationStore();
  const [localBook, localBookDispatch] : [BookMutation, any] = useReducer(localBookReducer, bookStore.payload); // zustand store       
  const [editMode, setEditMode] = useState<string>("base"); // base, content
  const requestBookReviewBtnRef = useRef<HTMLButtonElement>(null); //    ref


  // outline  ,   state bookMutation   
  useEffect(() => {
    if(outline){
      localBookDispatch({type: 'TITLE', payload: outline.title});
    }
    
    if(outline.coverImgUrl){
      localBookDispatch({type: 'COVER_IMG', payload: null});
    }
  }, [outline]);

  
  //    zustand store 
  useEffect(() => {

    // local title   outline title    => title  
    if(localBook.title && localBook.title !== outline.title && outline.title !== " ..."){
      console.log(`[] ${outline.title} -> ${localBook.title}`)
      bookStore.dispatchs.setTitle(localBook.title);
    }
    
    // local coverImg  null  => coverImg  
    if(localBook.coverImg){
      bookStore.dispatchs.setCoverImg(localBook.coverImg);
    }
  }, [localBook]);


  return (
  <>
      <div>
        <div className="grid max-w-sm grid-cols-3 gap-1 p-1 mx-auto my-2 bg-gray-200 rounded-lg" role="group">
            <button type="button" onClick={() => setEditMode("base")} className={ (editMode === "base" ? "text-white bg-gray-900" : "text-gray-900 hover:bg-gray-300") + " px-5 py-1.5 text-xs font-medium rounded-lg"}>
               
            </button>
            <button type="button" onClick={() => setEditMode("reviewRequest")} className={ (editMode === "reviewRequest" ? "text-white bg-gray-900" : "text-gray-900 hover:bg-gray-300") + " px-5 py-1.5 text-xs font-medium rounded-lg"}>
               
            </button>
            <button type="button" onClick={() => setEditMode("deleteBook")} className={ (editMode === "deleteBook" ? "text-white bg-gray-900" : "text-gray-900 hover:bg-gray-300") + " px-5 py-1.5 text-xs font-medium rounded-lg"}>
              
            </button>
        </div>
      </div>
  
        { editMode === "base" && 
          <div className="my-16">
            <div>
              {/* title */}
              <div className="sm:col-span-2">
                  <label htmlFor="title" className="block mb-2 text-md font-medium text-gray-900"> </label>
                  <input value={localBook.title !== null ? localBook.title : ''} onChange={(e) => localBookDispatch({type: "TITLE", payload: e.target.value})} type="text" name="title" id="title" className="bg-gray-50 border border-gray-300 text-gray-900 text-xl rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5" placeholder="  ." />
              </div>
              <br /><br />
              {/* coverImg */}
              <div className="block mb-2 text-md font-medium text-gray-900">  </div>
              <ImageCropComponent defaultSrc={outline.coverImgUrl} cropedFilename={`book-${bookId}-coverImg`} setFileDate={localBookDispatch} />
            </div>
          </div>
        }
  
        { editMode === "reviewRequest" &&
        <>
          <div className="mt-16">
            <button className="btn btn" onClick={() => {window.location.href = `/viewer/${bookId}`}}>  </button>
            <p className="mt-1">        Preview </p>
          </div>

          <div className="mt-16 text-black">
            <p>  ,      .</p>
            <p>  ,   .</p>
            <p>    .</p>
            <button className="btn btn-neutral" onClick={() => {requestBookReview()}} ref={requestBookReviewBtnRef}></button>
          </div>
        </>
        }

        { editMode === "deleteBook" &&
        <>
          <div className="mt-16 text-black">
            <p> ?     .</p>
            <p>   <i></i>     </p>
            <input className="mt-2 input input-bordered input-error bg-white mr-2" type="text" placeholder="" />
            <button className="btn btn-error btn-outline" onClick={deleteBook} ></button>
          </div>
        </>
        }
      </>
  );



  function localBookReducer(state : BookMutation, action : {type : string, payload : string | File}) : any {
    switch(action.type){
      case 'TITLE':
        return {
          ...state,
          title: action.payload as string
        }
      case 'COVER_IMG':
        return {
          ...state,
          coverImg: action.payload as File
        }
      default:
        return state;
    }
  }

  //  
  function requestBookReview(){
    if(requestBookReviewBtnRef.current){
      requestBookReviewBtnRef.current.classList.add("loading");
      requestBookReviewBtnRef.current.classList.add("loading-spinner");
    }

    // eslint-disable-next-line no-restricted-globals
    if(!confirm("  ?")){
      return 
    }


    axios.put(`/api/books/${bookId}/status?status=REVIEW_REQUESTED`)
    .then(res => {
      if(res.data.status === "REVIEW_REQUESTED"){
        window.location.href = `/account/books`;
        localStorage.setItem("alertStorageKey", "success:   ! <br>    .");
      } else {
        throw new Error("  .");
      }
    })
    .catch(err => {
      flowAlert("  .");
      console.log(err);
    })
  }


  //  
  function deleteBook(){
    // eslint-disable-next-line no-restricted-globals
    if(!confirm(" ?")){
      return 
    }

    axios.delete(`/api/books/${bookId}`)
    .then(res => {
      if(res.status === 200){
        window.location.href = `/account/books`;
        localStorage.setItem("alertStorageKey", "success:  .");
      } else {
        throw new Error("  .");
      }
    })
    .catch(err => {
      flowAlert("  .");
      console.log(err);
    })
  }


}